import './App.css';
import QRCode from "react-qr-code"
import { Button, Form } from 'react-bootstrap';
import { useState } from 'react';

function App() {


  const [qrvalue, setQrValue] = useState("test");

  const azureM = {
    ClientId : "",
    AzureInstance: "",
    TenantId: "",
    BaseUrl: "",
    ReturnUrl: "",
    APIResourceURI: "",
  }

  const [clientId, setClientId] = useState();
  const [azureInstance, setAzureInstance] = useState();
  const [tenantId, setTenantId] = useState();
  const [baseUrl, setBaseUrl] = useState();
  const [returnUrl, setReturnUrl] = useState();
  const [apiResourceURi, setAPIResourceURI] = useState();

  const setQr = () =>{

    azureM.ClientId = clientId;
    azureM.AzureInstance = azureInstance;
    azureM.TenantId = tenantId;
    azureM.BaseUrl= baseUrl;
    azureM.ReturnUrl = returnUrl;
    azureM.APIResourceURI= apiResourceURi;
   
    console.log(JSON.stringify(azureM));

    setQrValue(JSON.stringify(azureM));
  }


  const clear = () =>{

    setClientId("")
    setAzureInstance("")
    setTenantId("")
    setBaseUrl("")
    setReturnUrl("")
    setAPIResourceURI("")
  }
 

  return (
    <div className="App">
      <header className="App-header">
       

      <div className='App-input'>
        <Form>
          <Form.Group className='mb-3'>
              <Form.Label>ClientID</Form.Label>
              <Form.Control type="text" defaultValue={clientId} onChange={(value) =>{setClientId(value.target.value)}}/>
          </Form.Group>

            <Form.Group className='mb-3'>
              <Form.Label>AzureInstance</Form.Label>
              <Form.Control type="text" defaultValue={azureInstance} onChange={(value) =>{setAzureInstance(value.target.value)}} />
            </Form.Group>

            <Form.Group className='mb-3'>
              <Form.Label>TenantId</Form.Label>
              <Form.Control type="text" defaultValue={tenantId} onChange={(value) =>{setTenantId(value.target.value)}} />
            </Form.Group>

            <Form.Group className='mb-3'>
              <Form.Label>BaseUrl</Form.Label>
              <Form.Control type="text" defaultValue={baseUrl} onChange={(value) =>{setBaseUrl(value.target.value)}} />
            </Form.Group>

            <Form.Group className='mb-3'>
              <Form.Label>ReturnUrl</Form.Label>
              <Form.Control type="text" defaultValue={returnUrl} onChange={(value) =>{ setReturnUrl(value.target.value)}} />
            </Form.Group>

              <Form.Group className='mb-3'>
                <Form.Label>APIResourceURI</Form.Label>
                <Form.Control type="text" defaultValue={apiResourceURi} onChange={(value) =>{setAPIResourceURI(value.target.value)}} />
              </Form.Group>

          <Button onClick={()=>setQr()}>Create QR</Button>
        </Form>
        
      </div>

      <QRCode value={qrvalue} />

      </header>
    </div>
  );
}

export default App;
